import { F_CLEAR_SUCCESS, F_SET_SUCCESS } from "../types";

const initialState = {
    success: null,
}

export const fileReducer = (state = initialState, action: { type: string, payload: any }) => {
    switch (action.type) {
        case F_SET_SUCCESS:
            return { ...state, success: action.payload }
        case F_CLEAR_SUCCESS:
            return { ...state, success: null }
        default:
            return state;
    }
}