export const getLastUrl = (url) => {
	const split = url.split('/');
	return split[split.length - 1];
};

export class LocalAPIArray {
	constructor(localApis) {
		this.localApis = localApis ? localApis : [];
	}

	getLocalApis() {
		return this.localApis;
	}
}

export class LocalAPI {
	constructor(key, objName) {
		this.key = key;
		this.objName = objName;
	}

	getKey() {
		return this.key;
	}

	getObjName() {
		return this.objName;
	}
}

export const areUrlsEqual = (url1, url2) => {
	try {
		const url1Array = url1.split('/').filter((url) => !isStringEmpty(url));
		const url2Array = url2.split('/').filter((url) => !isStringEmpty(url));

		return url1Array[url1Array.length - 1] === url2Array[url2Array.length - 1];
	} catch {
		return false;
	}
};

export const getDynamicRejectedBy = (taskObject) => {
	if (taskObject && taskObject.rejectedBy) {
		const rejectedUser = taskObject.rejectedBy.find((user) => user.status === 'Y');
		if (rejectedUser) {
			return rejectedUser.userType;
		}
	}
	return '';
};

export const getRejectedBy = (taskObject) => {
	const rejectedFields = taskObject
		? (({ rejectedA, rejectedB, rejectedC, rejectedD, rejectedAD, rejectedE, rejectedF, rejectedG }) => ({
				rejectedA,
				rejectedB,
				rejectedC,
				rejectedD,
				rejectedAD,
				rejectedE,
				rejectedF,
				rejectedG,
		  }))(taskObject)
		: {};

	const rejected = Object.keys(rejectedFields).find((field) => rejectedFields[field] === 'Y');

	if (rejected) {
		switch (rejected) {
			case 'rejectedC':
				return UserType.ADMIN;
			case 'rejectedB':
				return UserType.SUB_ENGINEER;
			case 'rejectedD':
				return UserType.DESIGNER;
			case 'rejectedA':
				return UserType.ENGINEER;
			case 'rejectedAD':
				return UserType.AMIN;
			case 'rejectedE':
				return UserType.POSTE;
			case 'rejectedF':
				return UserType.POSTF;
			case 'rejectedG':
				return UserType.POSTG;
			default:
				return '';
		}
	} else {
		return '';
	}
};

export const getNextDynamicUrl = (currentFormId, step = 1) => {
	const menuList = JSON.parse(getLocalStorage(MENU_LIST));
	let url = '',
		id;
	// console.log('menu', menuList);
	menuList.forEach((row, index) => {
		if (String(row.id) === String(currentFormId)) {
			if (menuList.length > index + step) {
				url = menuList[index + step].viewURL;
				id = menuList[index + step].id;
				if (url === '/user/forms/forward-to-next') {
					url += `/${menuList[index + step].groupId}`;
				}
			}
		}
	});
	return { forward: !id, url: url.trim(), id };
};
